<template>
  <li
    @click="
      $emit('music-player', item);
      $emit('update-song-list', newSong);
    "
    :class="{ active: item.id === songId }"
  >
    <div class="fl">
      <h3>{{ item.name }}</h3>
      <p>
        <span></span>{{ item.song.artists[0].name }}-{{ item.song.album.name }}
      </p>
    </div>
    <div v-if="item.id !== songId" class="fr"></div>
    <div v-else class="fr active" :class="{ playing: playing }">
      <span></span>
      <span></span>
      <span></span>
      <span></span>
    </div>
  </li>
</template>

<script>
export default {
  props: {
    newSong: Array,
    item: Object,
    songId: [Number, String],
    playing: Boolean,
  },
  methods: {},
};
</script>

<style lang="less" scoped>
li {
  border-bottom: 1rem solid rgb(223, 218, 218);
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-left: 5rem;

  .fl {
    width: calc(100vw - 30rem);
    padding: 6rem 0;

    h3 {
      width: calc(100vw - 40rem);
      font-size: 17rem;
      color: black;
      overflow-x: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
    }
    p {
      font-size: 12rem;
      color: #888888;
      display: flex;
      align-items: center;

      span {
        display: block;
        background: url("../assets/index_icon_2x.png") no-repeat 0 0;
        width: 12px;
        height: 8px;
        background-size: 165rem;
        margin-right: 5rem;
      }
    }
  }
  .fr {
    height: 22px;
    width: 22px;
    background: url("../assets/index_icon_2x.png") no-repeat -23px 0;
    background-size: 160rem;
    margin-right: 10rem;

    &.active {
      background-image: none;
      display: flex;
      justify-content: space-around;
      span {
        width: 10.5%;
        height: 100%;
        background: red;
        animation: playingrunning 0.8s infinite alternate;
        animation-play-state: paused;
        transform-origin: bottom;
        &:nth-child(1) {
          animation-delay: 0;
        }
        &:nth-child(2) {
          animation-delay: -0.2s;
        }
        &:nth-child(3) {
          animation-delay: -0.4s;
        }
        &:nth-child(4) {
          animation-delay: -0.6s;
        }
      }
      &.playing {
        span {
          animation-play-state: running;
        }
      }
    }
  }
}

@keyframes playingrunning {
  from {
    transform: rotateX(15deg);
  }
  to {
    transform: rotateX(60deg);
  }
}
</style>