<template>
  <div class="playinglist">
    <div class="mask" @click="$emit('quit-PlayingSongList')"></div>
    <div class="content">
      <ul class="card">
        <h3>
          当前播放<span>（{{ songlist.length }}）</span>
        </h3>
        <li v-for="song in songlist" :key="song.id">
          <div class="fr">
            <div
              v-if="song.id == songId"
              class="tiaodong active"
              :class="{ playing: playing }"
            >
              <span></span>
              <span></span>
              <span></span>
              <span></span>
            </div>
            <p
              :style="{ color: song.id === songId ? 'red' : '' }"
              @click.stop="$emit('update-song', song)"
            >
              {{ song.name }} -

              <span v-if="song.al">{{ song.al.name }}</span>

              <span
                v-else
                v-for="songer in song.song.artists"
                :key="songer.id"
                :style="{ color: song.id === songId ? 'red' : '' }"
                @click.stop="$emit('update-song', song)"
              >
                {{ songer.name }}
              </span>
            </p>
          </div>
          <div class="fl">
            <span
              class="icon"
              @click.stop="$emit('delete-playingSongList', song.id)"
            >
              <i class="fa fa-times" aria-hidden="true"></i>
            </span>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    songlist: Array,
    songId: [Number, String],
    playing: Boolean,
  },
};
</script>

<style lang="less" scoped>
.playinglist {
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;

  z-index: 9;

  border-radius: 15rem;
  .mask {
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
  }

  div.content {
    width: 95%;
    height: 60vh;
    overflow-y: scroll;
    position: absolute;
    bottom: 60rem;
    left: 0;
    right: 0;
    margin: auto;
    background: #ffffff;
    border-radius: 15rem;

    .card {
      width: 95%;
      min-height: calc(60vh - 30rem);
      padding: 15rem;
      h3 {
        font-size: 18rem;
        font-weight: bold;
        span {
          font-size: 10rem;
          color: #b1b1b1;
        }
      }
      li {
        height: 45rem;
        display: flex;
        justify-content: space-between;
        align-items: center;

        div.fr {
          height: 45rem;
          display: flex;
          align-items: center;
          .tiaodong {
            height: 22px;
            width: 22px;
            margin-right: 10rem;

            &.active {
              background-image: none;
              display: flex;
              justify-content: space-around;
              span {
                width: 10.5%;
                height: 100%;
                background: red;
                animation: playingrunning 0.8s infinite alternate;
                animation-play-state: paused;
                transform-origin: bottom;
                &:nth-child(1) {
                  animation-delay: 0;
                }
                &:nth-child(2) {
                  animation-delay: -0.2s;
                }
                &:nth-child(3) {
                  animation-delay: -0.4s;
                }
                &:nth-child(4) {
                  animation-delay: -0.6s;
                }
              }
              &.playing {
                span {
                  animation-play-state: running;
                }
              }
            }
          }
          p {
            height: 45rem;
            line-height: 45rem;
            font-size: 16rem;
            span {
              font-size: 10rem;
              color: #b1b1b1;
            }
          }
        }
        div.fr {
          span {
            i {
              font-size: 16rem;
              color: #b1b1b1;
            }
          }
        }
      }
    }
  }
}
@keyframes playingrunning {
  from {
    transform: rotateX(15deg);
  }
  to {
    transform: rotateX(60deg);
  }
}
</style>