<template>
  <div class="Disc">
    <div class="needle" :class="{ active: playing }"></div>
    <div class="img" :class="{ playing: playing }">
      <img
        v-if="song.al"
        :class="{ playing: playing }"
        :src="
          song ? `${song?.al.picUrl}?imageView=1&type=webp&thumbnail=200x0` : ''
        "
        alt=""
      />

      <img
        v-else
        :src="
          song ? `${song?.picUrl}?imageView=1&type=webp&thumbnail=200x0` : ''
        "
        alt=""
      />
    </div>
  </div>
</template>

<script>
export default {
  props: {
    song: Object,
    playing: Boolean,
  },
};
</script>

<style lang="less" scoped>
.Disc {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;

  .needle {
    width: 200rem;
    height: 200rem;
    position: absolute;
    top: 0;
    left: 85rem;
    right: 0;
    margin: auto;
    background: url(../assets/needle-ab.png) center no-repeat;
    background-size: 60%;
    z-index: 9;
    transform-origin: 60rem 20rem;
    transform: rotate(-15deg);
    transition: all .3s;

    &.active {
      transform: rotate(0deg);
    }
  }

  .img {
    width: 300rem;
    height: 300rem;
    background: lightcoral;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    background: url(../assets/border.png) center no-repeat;
    background-size: 100%;

    animation: rotate 8s linear infinite;

    animation-play-state: paused;

    &.playing {
      animation-play-state: running;
    }
    img {
      border-radius: 50%;
    }
  }
}

@keyframes rotate {
  form {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
</style>