<template>
  <div class="musicplayer" v-if="song">
    <div
      v-if="song.al"
      class="bg"
      :style="`background:url(${song?.al.picUrl}?imageView=1&type=webp&thumbnail=375x0) center no-repeat; background-size: cover;`"
    ></div>
    <div
      v-else
      class="bg"
      :style="`background:url(${song?.picUrl}?imageView=1&type=webp&thumbnail=375x0) center no-repeat; background-size: cover;`"
    ></div>

    <header>
      <div class="msg">
        <h3>{{ song?.name }}</h3>

        <p v-if="song.al">{{ song.al.name }}</p>

        <p v-else v-for="songer in song.song.artists" :key="songer.id">
          {{ songer.name }}
        </p>
      </div>
      <span @click="$parent.showPlayer = false"
        ><i class="fa fa-angle-down" aria-hidden="true"></i
      ></span>
    </header>
    <section>
      <PlayDisc
        v-if="showDisc"
        @click.native="showDisc = false"
        :song="song"
        :playing="playing"
      ></PlayDisc>
      <PlayLyric
        v-else
        @click.native="showDisc = true"
        :song="song"
        :currentTime="currentTime"
      ></PlayLyric>
    </section>
    <footer>
      <div class="jindutiao">
        <p>
          <span>{{ cmin }}</span> : <span>{{ csec }} </span>
        </p>
        <progressBar
          :duration="duration"
          :currentTime="currentTime"
          @update-time="$emit('update-time', $event)"
        ></progressBar>

        <p>
          <span>{{ dmin }}</span> : <span>{{ dsec }}</span>
        </p>
      </div>

      <div class="btn">
        <!-- <div class="pattern" @click="$emit('change-model')">
          {{ model }}
        </div> -->

        <div class="pattern" @click="$emit('change-model')">
          <svg
            v-if="'顺' == model ? true : false"
            class="icon"
            width="25px"
            height="25px"
            viewBox="0 0 1024 1024"
            version="1.1"
            xmlns="http://www.w3.org/2000/svg"
