              fill="#ffffff"
              d="M682.48 297.633333L332.666667 752.386667A149.333333 149.333333 0 0 1 214.306667 810.666667H64a21.333333 21.333333 0 0 1 0-42.666667h150.306667a107.333333 107.333333 0 0 0 84.546666-41.633333L648.666667 271.613333A149.333333 149.333333 0 0 1 767.026667 213.333333h141.473333l-48.92-48.913333a21.333333 21.333333 0 0 1 30.173333-30.173333l85.333334 85.333333a21.333333 21.333333 0 0 1 0 30.173333l-85.333334 85.333334a21.333333 21.333333 0 0 1-30.173333-30.173334l48.92-48.913333h-141.473333a107.333333 107.333333 0 0 0-84.546667 41.633333zM64 256h150.306667a107.333333 107.333333 0 0 1 84.546666 41.633333l109.26 142.04a21.333333 21.333333 0 0 0 33.82-26L332.666667 271.613333A149.333333 149.333333 0 0 0 214.306667 213.333333H64a21.333333 21.333333 0 0 0 0 42.666667z m825.753333 432.913333a21.333333 21.333333 0 0 0-30.173333 30.173334l48.92 48.913333h-141.473333a107.333333 107.333333 0 0 1-84.546667-41.633333L573.22 584.326667a21.333333 21.333333 0 0 0-33.82 26L648.666667 752.386667a149.333333 149.333333 0 0 0 118.36 58.28h141.473333l-48.92 48.913333a21.333333 21.333333 0 0 0 30.173333 30.173333l85.333334-85.333333a21.333333 21.333333 0 0 0 0-30.173333z"
            />
          </svg>
        </div>

        <div class="prve" @click="$emit('change-song', 'prev')">
          <i class="fa fa-step-backward" aria-hidden="true"></i>
        </div>
        <div class="isplaying">
          <span v-if="!playing" @click.stop="$emit('song-play')">
            <i class="fa fa-play" aria-hidden="true"></i>
          </span>
          <span v-else @click.stop="$emit('song-pause')">
            <i class="fa fa-pause" aria-hidden="true"></i>
          </span>
        </div>
        <!-- <button>下</button> -->
        <div class="next" @click="$emit('change-song', 'next')">
          <i class="fa fa-step-forward" aria-hidden="true"></i>
        </div>
        <div class="favorite">
          <i class="fa fa-heart-o" aria-hidden="true"></i>
        </div>
        <div class="list" @click.stop="$emit('toggle-showPlayingList')">
          <i class="fa fa-bars" aria-hidden="true"></i>
        </div>
      </div>
    </footer>
  </div>
</template>

<script>
import PlayDisc from "@/components/PlayDisc.vue";
import PlayLyric from "@/components/PlayLyric.vue";
import progressBar from "@/components/ProgressBar.vue";

export default {
  components: {
    PlayDisc,
    PlayLyric,
    progressBar,
  },
  data() {
    return {
      showDisc: true,
    };
  },

  props: {
    song: Object,
    playing: Boolean,
    duration: Number,
    currentTime: Number,
    model: String,
  },
  computed: {
    cmin: function () {
      return parseInt(this.currentTime / 60) < 10
        ? "0" + parseInt(this.currentTime / 60)
        : "00";
    },
    csec: function () {
      return parseInt(this.currentTime % 60) < 10
        ? "0" + parseInt(this.currentTime % 60)
        : parseInt(this.currentTime % 60);
    },
    dmin: function () {
      return parseInt(this.duration / 60) < 10
        ? "0" + parseInt(this.duration / 60)
        : "00";
    },
    dsec: function () {
      return parseInt(this.duration % 60) < 10
        ? "0" + parseInt(this.duration % 60)
        : parseInt(this.duration % 60);
    },
  },
};
</script>

<style lang="less" scoped>
.musicplayer {
  width: 100%;
  height: 100%;
  background-color: rgba(175, 175, 175);
  position: fixed;
  top: 0;
  left: 0;
  z-index: 9;
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  background-size: cover;
  background-position: 100%;
  background-repeat: no-repeat;

  .bg {
    width: 100vw;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;

    background-repeat: no-repeat;
    background-size: cover;
    background-position: 50%;
    filter: blur(20rem);
    transform: scale(1.5);
    z-index: -1;
  }

  .bg::after {
    content: " ";
    background-color: rgba(0, 0, 0, 0.322);
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    z-index: 1;
    height: 100%;
    width: 100%;
  }

  header {
    width: 100vw;
    display: flex;
    // background: lightblue;
    justify-content: center;
    position: relative;
    padding-top: 10rem;

    .msg {
      h3 {
        color: #ffffff;
        font-size: 16rem;
        text-align: center;
      }
      p {
        display: flex;
        justify-content: center;
        align-items: center;
        color: #bfbfbf;
        font-size: 14rem;
      }
    }
    span {
      position: absolute;
      top: 20rem;
      left: 20rem;
      color: white;
      i {
        font-size: 25rem;
      }
    }
  }
  section {
    width: 100vw;
    flex-grow: 1;
  }
  footer {
    width: 100vw;
    height: 100rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    .jindutiao {
      width: 100vw;
      display: flex;
      justify-content: space-around;
      align-items: center;
      label {
        width: 250px;
        height: 5rem;
        display: block;
        position: relative;
        // overflow: hidden;
        z-index: 9;
        input[type="range"] {
          width: 250px;
          height: 100%;
          display: block;
          opacity: 0;
          margin: 0;
        }
        .jindu {
          width: 250px;
          height: 60%;
          background-color: #929290;
          border-radius: 3px;
          position: absolute;
          top: 0;
          left: 0;
          bottom: 0;
          margin: auto;
          z-index: -1;
          .mask {
            width: 0px;
            height: 100%;
            background-color: #b82626;
            border-radius: 3px 0 0 3px;
          }
          .dot {
            width: 4px;
            height: 4px;
            background-color: #b82626;
            border: 5px solid white;
            border-radius: 50%;
            position: absolute;
            top: 0;
            left: 0;
            bottom: 0;
            margin: auto;
          }
        }
      }
      p {
        color: #929290;
      }
    }

    .btn {
      width: 100vw;
      display: flex;
      justify-content: space-around;
      margin-top: 20rem;

      .pattern {
        display: flex;
        justify-content: center;
        align-items: center;
      }
      .prve,
      .next {
        width: 40rem;
        height: 40rem;
        display: flex;
        justify-content: center;
        align-items: center;
        i {
          font-size: 20rem;
          color: white;
        }
      }
      .isplaying {
        span {
          width: 40rem;
          height: 40rem;
          border: 2rem solid #e7e7e5;
          background: transparent;
          border-radius: 50%;
          display: flex;
          justify-content: center;
          align-items: center;
          i {
            color: #ffffff;
            font-size: 20rem;
          }
        }
      }

      .favorite {
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 20rem;
        color: white;
      }

      .list {
        display: flex;
        justify-content: center;
        align-items: center;
        i {
          font-size: 20rem;
          color: white;
        }
      }
    }
  }
}
</style>