<template>
  <div class="playlyric">
    <div class="box">
      <ul ref="ul" v-if="parsed.length">
        <LyricText
          v-for="(p, index) in parsed"
          :key="p.time"
          :p="p"
          :currentTime="currentTime"
          :o="o"
          :index="index"
          :lyricIndex="lyricIndex"
        ></LyricText>
      </ul>
    </div>
  </div>
</template>

<script>
import LyricText from "@/components/LyricText.vue";
export default {
  components: {
    LyricText,
  },
  props: {
    song: Object,
    currentTime: [Number, String],
  },
  data() {
    return {
      lyric: "",
      parsed: [],
      o: null,
      lyricIndex: "",
      liHeigth: [],
    };
  },

  created() {
    this.axios("https://apis.netstart.cn/music/lyric", {
      params: {
        id: this.song.id,
      },
    }).then((res) => {
      this.lyric = res.data.lrc.lyric;

      var arr = this.lyric.split("\n");

      this.parsed = arr
        .filter((o) => o)
        .map((o) => {
          var { groups } = o.match(
            /\[(?<mm>\d{2}):(?<ss>\d{2}\.\d{2,3})\](?<text>.*)/
          );
          return {
            time: groups.mm * 60 + groups.ss * 1,
            text: groups.text.trim(),
          };
        });
    });
  },
  methods: {
    findCurrentLyric: function (parsed, currentTime) {
      var copy = [...parsed].reverse();
      var index = copy.findIndex((o) => o.time < currentTime);

      return {
        index: copy.length - 1 - index,
        text: copy[index].text,
      };
    },
  },
  watch: {
    currentTime: function (n) {
      if (this.parsed.length) {
        this.o = this.findCurrentLyric(this.parsed, n);
        this.lyricIndex = this.o.index;
        this.$refs.ul.style.transform = `translateY(${
          this.o.index * -21 + 150
        }px)`;
      }
    },
  },
};
</script>

<style lang="less" scoped>
.playlyric {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  .box {
    width: 250px;
    height: 400px;
    overflow-y: scroll;

    &::-webkit-scrollbar{
      display: none;
    }
    ul {
      width: 250px;
      height: 400px;
      margin: 0;
      transform: translateY(150px);
    }
  }
}
</style>