<template>
  <div class="musicplayer">
    <div class="fl">
      <div class="bg">
        <img
          v-if="song.al"
          :class="{ playing: playing }"
          :src="
            song ? `${song.al.picUrl}?imageView=1&type=webp&thumbnail=30x0` : ''
          "
          alt=""
        />
        <img
          v-else
          :class="{ playing: playing }"
          :src="
            song ? `${song.picUrl}?imageView=1&type=webp&thumbnail=30x0` : ''
          "
          alt=""
        />
      </div>
      <h3>
        {{ song?.name }}

        <span v-if="song.al">{{ song.al.name }}</span>

        <span v-else> - {{ song.song.artists[0].name }}</span>
      </h3>
    </div>
    <div class="fr">
      <div class="isplaying">
        <button v-if="!playing" @click.stop="$emit('song-play')">
          <i class="fa fa-play" aria-hidden="true"></i>
        </button>
        <button v-else @click.stop="$emit('song-pause')">
          <i class="fa fa-pause" aria-hidden="true"></i>
        </button>

        <canvas id="mycanvas" ref="canvas" width="30" height="30"></canvas>
      </div>

      <div class="list" @click.stop="$emit('toggle-showPlayingList')">
        <i class="fa fa-bars" aria-hidden="true"></i>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    song: null,
    playing: Boolean,
    duration: Number,
    currentTime: Number,
  },
  computed: {
    percentage: function () {
      return this.currentTime / this.duration;
    },
  },
  watch: {
    percentage: function (n) {
      var c = this.$refs.canvas;
      var ctx = c.getContext("2d");
      ctx.clearRect(0, 0, 30, 30);

      ctx.beginPath();
      ctx.strokeStyle = "aqua";
      ctx.arc(15, 15, 14, -0.5 * Math.PI, Math.PI * (2 * n - 0.5), false);
      ctx.stroke();
    },
  },
};
</script>

<style lang="less" scoped>
.musicplayer {
  width: 100vw;
  height: 60rem;
  position: fixed;
  bottom: 0;
  left: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 10rem;
  background-color: #fdfdfd;
  border-top: 1rem solid rgba(0, 0, 0, 0.1);

  div.fl {
    display: flex;
    align-items: center;

    div.bg {
      width: 50rem;
      height: 50rem;
      background: url(../assets/border.png) center no-repeat;
      background-size: 50rem;
      position: relative;
      img {
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        margin: auto;
        border-radius: 50%;
        animation: rotate 8s linear infinite;
        animation-play-state: paused;
        &.playing {
          animation-play-state: running;
        }
      }
    }
    h3 {
      width: calc(100vw - 150rem);
      margin-left: 10rem;
      flex-shrink: 0;
      white-space: nowrap;
      overflow: hidden;
      span {
        font-size: 10rem;
      }
    }
  }
  div.fr {
    display: flex;
    align-items: center;
    .isplaying {
      display: flex;
      justify-content: space-around;
      align-items: center;
      position: relative;
      margin-right: 25rem;
      button {
        width: 30rem;
        height: 30rem;
        border: 1rem solid rgba(0, 0, 0, 0.1);
        background: transparent;
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        position: absolute;
        top: 0;
        left: 0;
        i {
          color: black;
          font-size: 16rem;
        }
      }
      #mycanvas {
        border-radius: 50%;
      }
    }
    .list {
      display: flex;
      justify-content: center;
      align-items: center;
      i {
        font-size: 20rem;
        color: black;
      }
    }
  }
}

@keyframes rotate {
  form {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
</style>