<template>
  <div id="app">
    <div class="header" v-if="$route.meta.showNav">
      <div class="logo">
        <div class="img"><img src="./assets/logo.png" alt="" /></div>
        <h3>网易云音乐</h3>
      </div>
      <a href=""> 下载APP </a>
    </div>
    <nav v-if="$route.meta.showNav">
      <router-link to="/">推荐音乐</router-link>
      <router-link to="/hot">热歌榜</router-link>
      <router-link to="/search">搜索</router-link>
    </nav>
    <router-view
      :songId="song?.id"
      :playing="playing"
      @music-player="song = $event"
      @update-song-list="songlist = $event"
      :style="{ marginBottom: song ? '60rem' : 0 }"
    />
    <audio
      v-show="song"
      :src="songSrc"
      autoplay
      ref="audio"
      @play="playing = true"
      @pause="playing = false"
      @durationchange="duration = $event.target.duration"
      @timeupdate="currentTime = $event.target.currentTime"
      @ended="changeSong('next')"
    ></audio>
    <Miniplayer
      v-if="song"
      :song="song"
      :playing="playing"
      @song-play="$refs.audio.play()"
      @song-pause="$refs.audio.pause()"
      :duration="duration"
      :currentTime="currentTime"
      @click.native="showPlayer = true"
      @toggle-showPlayingList="showplayinglist = !showplayinglist"
    ></Miniplayer>

    <MusicPlayer
      v-if="song && showPlayer"
      :song="song"
      :playing="playing"
      @song-play="$refs.audio.play()"
      @song-pause="$refs.audio.pause()"
      :duration="duration"
      :currentTime="currentTime"
      @update-time="$refs.audio.currentTime = $event"
      @toggle-showPlayingList="showplayinglist = !showplayinglist"
      @change-song="changeSong"
      :model="model"
      @change-model="changeModel"
    ></MusicPlayer>

    <PlayingsongList
      v-show="showplayinglist"
      :songlist="songlist"
      :songId="song?.id"
      @update-song="song = $event"
      @delete-playingSongList="deleteplayingsonglist"
      @quit-PlayingSongList="showplayinglist = !showplayinglist"
      :playing="playing"
    ></PlayingsongList>
  </div>
</template>
<script>
import Miniplayer from "@/components/MiniMusicPlayer.vue";
import MusicPlayer from "@/components/MusicPlayer.vue";
import PlayingsongList from "@/components/PlayingsongList.vue";

export default {
  components: {
    Miniplayer,
    MusicPlayer,
    PlayingsongList,
  },
  data() {
    return {
      song: JSON.parse(localStorage.getItem("song")),
      playing: false,
      duration: 0,
      currentTime: Number(localStorage.getItem("currentTime")) || 0,
      showPlayer: false,
      songlist: JSON.parse(localStorage.getItem("songlist")) || [],
      showplayinglist: false,
      models: ["顺", "随", "单"],
      model: "顺",
    };
  },
  computed: {
    songSrc: function () {
      return this.song
        ? `https://music.163.com/song/media/outer/url?id=${this.song.id}.mp3`
        : null;
    },
  },
  mounted() {
    this.$refs.audio.currentTime = this.currentTime;
  },
  methods: {
    changeSong: function (direction) {
      console.log(direction);

      switch (this.model) {
        case "顺":
          var currentSongIndex = this.songlist.findIndex(
            (o) => o.id === this.song.id
          );

          if (direction === "next") {
            this.song =
              currentSongIndex === this.songlist.length - 1
                ? this.songlist[0]
                : this.songlist[currentSongIndex + 1];
          } else {
            this.song =
              currentSongIndex === 0
                ? this.songlist[this.songlist.length - 1]
                : this.songlist[currentSongIndex - 1];
          }
          break;
        case "随":
          var daixuan = this.songlist.filter((o) => o.id !== this.song.id);
          this.song = daixuan[Math.floor(Math.random() * daixuan.length)];
          break;
        case "单":
          this.$refs.audio.currentTime = 0;
          this.$refs.audio.play();
          break;
      }
    },
    changeModel: function () {
      var index = this.models.findIndex((o) => o === this.model);
      this.model =
        index === this.models.length - 1
          ? this.models[0]
          : this.models[index + 1];
    },
    deleteplayingsonglist: function (id) {
      this.songlist = this.songlist.filter((o) => o.id != id);
    },
  },
  watch: {
    song: function (song) {
      localStorage.setItem("song", JSON.stringify(song));
    },
    songlist: function (songlist) {
      localStorage.setItem("songlist", JSON.stringify(songlist));
    },
    currentTime: function (currentTime) {
      localStorage.setItem("currentTime", JSON.stringify(currentTime));
    },
  },
};
</script>

