import Vue from "vue";
import VueRouter from "vue-router";
import HomeView from "../views/HomeView.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "home",
    component: HomeView,
    meta: {
      showNav: true,
    },
  },
  {
    path: "/hot",
    name: "hot",
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () =>
      import(/* webpackChunkName: "about" */ "../views/HotView.vue"),
    meta: {
      showNav: true,
    },
  },
  {
    path: "/search",
    name: "search",
    component: () =>
      import(/* webpackChunkName: "about" */ "../views/SearchView.vue"),
    meta: {
      showNav: true,
    },
  },
  {
    path: "/playlist",
    name: "playlist",
    component: () =>
      import(/* webpackChunkName: "about" */ "../views/PlaylistView.vue"),
  },
  // MusicPlayer


];

const router = new VueRouter({
  routes,
});

export default router;
