<template>
  <div class="home">
    <h3>编辑推荐</h3>
    <ul class="home-card">
      <HomeCard
        v-for="item in recommendSong"
        :key="item.id"
        :item="item"
      ></HomeCard>
    </ul>
    <h3>最新音乐</h3>
    <ul class="home-list">
      <HomeList
        v-for="item in newSong"
        :key="item.id"
        :item="item"
        @music-player="$emit('music-player', $event)"
        :songId="songId"
        :playing="playing"
        :newSong="newSong"
        @update-song-list="$emit('update-song-list', $event)"
      ></HomeList>
    </ul>

    <div class="loading" v-if="loading">
      <img src="../assets/下载.gif" alt="" />
    </div>
    <footer>
      <div class="container">
        <div class="content">
          <img src="../assets/footer-logo.png" alt="" />
          <p>网易云音乐</p>
        </div>
        <div class="btn">打开APP，发现更多好音乐 ></div>
        <p>网易公司版权所有&copy;1997-2022 杭州乐读科技有限公司运营</p>
      </div>
    </footer>
  </div>
</template>

<script>
import HomeCard from "@/components/HomeCard.vue";
import HomeList from "@/components/HomeList.vue";

export default {
  components: {
    HomeCard,
    HomeList,
  },
  data() {
    return {
      recommendSong: [],
      newSong: [],
      loading: false,
    };
  },
  props: {
    songId: [String, Number],
    playing: Boolean,
  },
  created() {
    this.loading = true;
    Promise.all([
      this.axios("https://apis.netstart.cn/music/personalized", {
        params: {
          limit: 6,
        },
      }),
      this.axios("https://apis.netstart.cn/music/personalized/newsong"),
    ]).then(([p, n]) => {
      this.loading = false;
      this.recommendSong = p.data.result;
      this.newSong = n.data.result;
    });
  },
};
</script>

