<template>
  <label for="">
    <input
      type="range"
      min="0"
      :max="duration"
      :value="tmpcurrentTime"
      @input="range"
      @change="
        draging = false;
        $emit('update-time', $event.target.value);
      "
      ref="input"
    />
    <div class="jindu">
      <div class="mask" ref="mask"></div>
      <div class="dot" ref="dot"></div>
    </div>
  </label>
</template>

<script>
export default {
  data() {
    return {
      draging: false,
      tmpcurrentTime: this.currentTime,
    };
  },

  props: {
    duration: Number,
    currentTime: Number,
  },
  mounted() {
    this.$refs.mask.style.width =
      (this.currentTime / this.duration) * 100 + "%";

    this.$refs.dot.style.left = (this.currentTime / this.duration) * 98 + "%";
  },
  methods: {
    range: function () {
      this.draging = true;
      this.$refs.mask.style.width = this.currentTime + "%";
    },
  },
  watch: {
    currentTime: function (n) {
      if (!this.draging) {
        this.tmpcurrentTime = n;
      }
      this.$refs.mask.style.width = (n / this.duration) * 100 + "%";
      this.$refs.dot.style.left = (n / this.duration) * 98 + "%";
    },
  },
};
</script>

<style lang="less" scoped>
label {
  width: 250px;
  height: 5rem;
  display: block;
  position: relative;
  // overflow: hidden;
  z-index: 9;
  input[type="range"] {
    width: 250px;
    height: 100%;
    display: block;
    opacity: 0;
    margin: 0;
  }
  .jindu {
    width: 250px;
    height: 60%;
    background-color: #929290;
    border-radius: 3px;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    margin: auto;
    z-index: -1;
    .mask {
      width: 0px;
      height: 100%;
      background-color: #b82626;
      border-radius: 3px 0 0 3px;
    }
    .dot {
      width: 4px;
      height: 4px;
      background-color: #b82626;
      border: 5px solid white;
      border-radius: 50%;
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      margin: auto;
    }
  }
}
</style>